import React, {useState, createRef} from 'react';
import {Link} from "react-router-dom";
import {Badge} from 'reactstrap';
import {Alert} from 'reactstrap';
const Apply=({ applies, scholarship, scholarId, index})=>{
    let expanderBody=createRef();
    const [isOpen, setIsOpen]= useState(false);
    const toggle=()=>setIsOpen(!isOpen);
    
    const toggleExpander=e=>{
        if(e.target.type==='button') return;
        if(!isOpen){
            toggle();
        }
        else{
            toggle();
        }
    }
    function returnedWhy(){ //     .  
        if(applies.state==="returned")
         {
             if(applies.priorGrade<scholarship.gradeLimit)
              return <Alert color="danger"> </Alert>;
         }
    }

    return[
        <tr key="main" onClick={toggleExpander}>
            
             <th className="th" scope="row">{applies.applyId}</th>
             <td><a className="applyId">{applies.applyId}<br/></a><a className="smalltitle">{applies.scholarName}</a></td>
    <td style={{width:'80px', verticalAlign:'middle'}}><a className="smallstate">
        {
            (applies.state==="unselected")? (<Badge color="danger"></Badge>):
                ((applies.state==="selected")? (<Badge color="success"></Badge>):(
                    (applies.state==="applyDone")? (<Badge color="primary"></Badge>):(<Badge color="warning"></Badge>)
                ))
        }
           
    </a></td>
                </tr>
      ,
      
      (isOpen && (applies.state==="returned"))&& (
          //state returned  .   state  (applies.id%4==1) .
          /*(isOpen &&(applies.state==="returned"))&&(
            
          )*/
        <tr className="expandable" key="tr-expander">
             <td classname="uk-background-muted" colSpan={6}>
                 <div ref={expanderBody} className="inner uk-grid">
                     <div className="uk-width3-4" style={{textAlign: 'center', marginLeft: '30%', marginRight: '30%'}}>
                         {/*  .  ,  ,   */}
                         <h6>
                         <Alert color="danger"> </Alert><br/>
                        <Alert color="danger"> </Alert><br/>
                        <Alert color="danger"> </Alert>
                        </h6> 
                         
                         {/*stateView*/}
    
                        
                     </div>
                 </div>
             </td>
        </tr>
       
      )
    
]
}

export default Apply;