import React, { useState, createRef } from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { Link } from "react-router-dom";

const Student = ({ key, index, student, onChange, scholarId, onSelect, scholarState }) => {
    let expanderBody = createRef();
    const [isOpen, setIsOpen] = useState(false);
    const [modal, setModal] = useState(false);
    const toggle = () => setIsOpen(!isOpen);
    const modalToggle = () => setModal(!modal);
    const selectToggle = e => {
      onSelect(e);
      setModal(!modal);
    }

  
    const toggleExpander = e => {
      if (e.target.type === 'button') return;
  
      if (!isOpen) {
        toggle();
        }
        else {
        toggle();
      }
    }

    // isSelect student   ??
    const isSelect = (student.state==='')
  
      return [
        <tr key="main" onClick={toggleExpander}>
          <td className="uk-text-nowrap">{student.studentId}.</td>
          <td>{student.name}</td>
          <td>{student.state}</td>
          <td>
            {
              scholarState==="close"? 
                <Button type='button' disabled
                style={{backgroundColor:"gray", border:"none"}}>
                  </Button>
                  :
               <Button onClick={modalToggle} type='button' 
                style={!isSelect? {backgroundColor:"#0B7527", border:"none"}:{backgroundColor:"#941216", border:"none"}}>
                 {!isSelect? "":"" }</Button>
            }
            </td>
          <Modal isOpen={modal} toggle={modalToggle}>
            <ModalHeader toggle={modalToggle}>??</ModalHeader>
            <ModalBody>
              {
                !isSelect? 
                `${student.name}  ?`:`${student.name}    ?`
              }
            </ModalBody>
            <ModalFooter>
              <Button color="primary" onClick={selectToggle} name={student.studentId}></Button>{' '}
              <Button color="secondary" onClick={modalToggle}></Button>
            </ModalFooter>
          </Modal>
          <td><Link to={`/students/${student.studentId}/${scholarId}`}><Button></Button></Link></td>
        </tr>,
      ];
    
  }

  export default Student;